.transactionHistory {
  padding: 0 20px;
  border-radius: 5px;
  width: 80%;
  border-collapse: collapse;
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.25);
  font-family: 'Helvetika';
  margin: 0 auto 50px;
}

.head th {
  background-color: rgb(0, 188, 212);
  color: #fff;
  font-size: 13px;
  text-transform: uppercase;
  padding: 20px 0;
}

.row td {
  text-align: center;
  font-size: 15px;
  padding: 18px 0;
  color: #5c6975;
  background-color: #fff;
  text-transform: capitalize;
}

.row:nth-of-type(2n) td {
  background-color: rgb(236, 242, 243);
}
